body {
  background-color: lightgray; }

* {
  margin: 0;
  padding: 0; }

html, body {
  width: 100%;
  height: 100%; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

#header, #footer {
  height: 10vh; }

#nav, #mainPart, #alerts {
  height: 60vh; }

.marginTop {
  margin-top: 3.9vh; }

.border {
  border: 1px solid black; }

.grid-display {
  width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

#studioTitle, #gameTitle {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  font-size: 10vh;
  text-align: center;
  text-shadow: 2px 1px white;
  color: navy;
  margin-top: 13vh; }

#sentence, #subGameTitle {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  font-size: 3.5vh;
  text-align: center;
  text-shadow: 2px 1px white;
  color: black;
  margin-top: 1vh; }

.animGameTitle {
  animation-name: showGameTitle;
  animation-duration: 6s;
  animation-timing-function: linear;
  animation-fill-mode: forwards; }

@keyframes showGameTitle {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.animGameTitleReverse {
  animation-name: hideGameTitle;
  animation-duration: 6s;
  animation-timing-function: linear;
  animation-fill-mode: forwards; }

@keyframes hideGameTitle {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

.beigeBackground {
  background-color: beige; }

.whiteGreenBackground {
  background: linear-gradient(white, green); }

.btnList {
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.mainStyleBtn {
  width: 15vh;
  height: 7.5vh;
  font-weight: bold;
  font-size: 3vh;
  border-radius: 10px; }
